import React, { useState, useEffect } from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import MenuItem from '@mui/material/MenuItem';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import Tooltip from '@mui/material/Tooltip';
import InputAdornment from '@mui/material/InputAdornment';

import { MoneyFormatInput, PercentFormat } from '../common/formats';
import { TextFieldCustom } from '../components/TextFieldCustom';
import { TextFieldCustomMoney } from '../components/TextFieldCustomMoney';
import { initCalculatedData, initInputData, taxTypes } from '../common/data';
import discount from '../calculations/discount';
import customerPrice from '../calculations/customerPrice';
import delivery from '../calculations/delivery';
import reward from '../calculations/reward.js';
import ebitda from '../calculations/ebitda';
import tax from '../calculations/tax';
import { earningsDirty, earningsNoDelivery, earningsNoTax } from '../calculations/earnings';

export default function Main() {
    const [state, setValue] = useState(initInputData);
    const [stateCalc, setCalcValue] = useState(initCalculatedData);
    const handleInputChange = (event) => {
        setValue({ ...state, [event.target.name]: event.target.value });
    };

    useEffect(() => {
        let calcData = {};
        calcData.discount = discount({
            price: Number(state.price),
            discount: Number(state.discount)
        });
        calcData.delivery = delivery({
            logisticsTariff: Number(state.logisticsTariff),
            redemption: Number(state.redemption)
        });
        calcData.customerPrice = customerPrice({
            price: Number(state.price),
            discount: calcData.discount
        });
        calcData.reward = reward({
            customerPrice: calcData.customerPrice,
            reward: Number(state.reward)
        });
        calcData.earningsDirty = earningsDirty({
            customerPrice: calcData.customerPrice,
            reward: calcData.reward
        });
        calcData.earningsNoDelivery = earningsNoDelivery({
            earningsDirty: calcData.earningsDirty,
            delivery: calcData.delivery
        });
        calcData.ebitda = ebitda({
            earningsNoDelivery: calcData.earningsNoDelivery,
            costPrice: state.costPrice
        });
        calcData.tax = tax({
            taxRate: Number(state.taxRate),
            taxType: state.taxType,
            customerPrice: calcData.customerPrice,
            ebitda: calcData.ebitda
        });
        calcData.earningsNoTax = earningsNoTax({
            ebitda: calcData.ebitda,
            tax: calcData.tax
        });

        setCalcValue(calcData);
    }, [state]);

    return (
        <Grid container rowSpacing={1.5} spacing={3}>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom
                    name="reward"
                    value={state.reward}
                    label="  WB, %"
                    InputProps={{
                        inputComponent: PercentFormat,
                        endAdornment: (
                            <InputAdornment position="end">
                                <Tooltip title="     ">
                                    <HelpOutlineIcon
                                        sx={{
                                            color: 'action.active',
                                            marginRight: (theme) => theme.spacing(1),
                                            marginY: (theme) => theme.spacing(0.5)
                                        }}
                                    />
                                </Tooltip>
                            </InputAdornment>
                        )
                    }}
                    onChange={handleInputChange}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom
                    name="logisticsTariff"
                    value={state.logisticsTariff}
                    label="  WB, "
                    InputProps={{
                        inputComponent: MoneyFormatInput
                    }}
                    onChange={handleInputChange}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom
                    name="price"
                    value={state.price}
                    label=" , "
                    InputProps={{
                        inputComponent: MoneyFormatInput,
                        endAdornment: (
                            <InputAdornment position="end">
                                <Tooltip title="    ">
                                    <HelpOutlineIcon
                                        sx={{
                                            color: 'action.active',
                                            marginRight: (theme) => theme.spacing(1),
                                            marginY: (theme) => theme.spacing(0.5)
                                        }}
                                    />
                                </Tooltip>
                            </InputAdornment>
                        )
                    }}
                    onChange={handleInputChange}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom
                    name="costPrice"
                    value={state.costPrice}
                    label=" , "
                    InputProps={{
                        inputComponent: MoneyFormatInput,
                        endAdornment: (
                            <InputAdornment position="end">
                                <Tooltip title="    ">
                                    <HelpOutlineIcon
                                        sx={{
                                            color: 'action.active',
                                            marginRight: (theme) => theme.spacing(1),
                                            marginY: (theme) => theme.spacing(0.5)
                                        }}
                                    />
                                </Tooltip>
                            </InputAdornment>
                        )
                    }}
                    onChange={handleInputChange}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom
                    name="discount"
                    value={state.discount}
                    label=", %"
                    InputProps={{
                        inputComponent: PercentFormat
                    }}
                    onChange={handleInputChange}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <Box sx={{ display: 'flex', alignItems: 'flex-end' }}>
                    <TextFieldCustom
                        name="redemption"
                        value={state.redemption}
                        label="  "
                        onChange={handleInputChange}
                        InputProps={{
                            inputComponent: PercentFormat,
                            endAdornment: (
                                <InputAdornment position="end">
                                    <Tooltip
                                        title={
                                            <React.Fragment>
                                                {'    , :'}
                                                <br />
                                                {'-   ,    30%'}
                                                <br />
                                                {'-    70-80%'}
                                            </React.Fragment>
                                        }
                                    >
                                        <HelpOutlineIcon
                                            sx={{
                                                color: 'action.active',
                                                marginRight: (theme) => theme.spacing(1),
                                                marginY: (theme) => theme.spacing(0.5)
                                            }}
                                        />
                                    </Tooltip>
                                </InputAdornment>
                            )
                        }}
                    />
                </Box>
            </Grid>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom
                    name="taxRate"
                    value={state.taxRate}
                    label=" , %"
                    InputProps={{
                        inputComponent: PercentFormat,
                        endAdornment: (
                            <InputAdornment position="end">
                                <Tooltip
                                    title={
                                        <React.Fragment>
                                            {' "",  '}
                                            <br />
                                            {'  ""    '}
                                        </React.Fragment>
                                    }
                                >
                                    <HelpOutlineIcon
                                        sx={{
                                            color: 'action.active',
                                            marginRight: (theme) => theme.spacing(1),
                                            marginY: (theme) => theme.spacing(0.5)
                                        }}
                                    />
                                </Tooltip>
                            </InputAdornment>
                        )
                    }}
                    onChange={handleInputChange}
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <TextFieldCustom select name="taxType" value={state.taxType} label=" " onChange={handleInputChange}>
                    {taxTypes.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextFieldCustom>
            </Grid>

            <Grid item xs={4}>
                <Typography align="center"></Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography align="center"></Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography align="center"></Typography>
            </Grid>

            <Grid item xs={4}>
                <Grid container rowSpacing={1.5} spacing={3}>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label="   " value={state.price} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label="  " value={stateCalc.discount} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label="  " value={stateCalc.customerPrice} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label=" WB" value={stateCalc.reward} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustom
                            label=""
                            value={stateCalc.earningsDirty}
                            InputProps={{
                                readOnly: true,
                                inputComponent: MoneyFormatInput,
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <Tooltip title="      WB">
                                            <HelpOutlineIcon
                                                sx={{
                                                    color: 'action.active',
                                                    marginRight: (theme) => theme.spacing(1),
                                                    marginY: (theme) => theme.spacing(0.5)
                                                }}
                                            />
                                        </Tooltip>
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={4}>
                <Grid container rowSpacing={1.5} spacing={3}>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label=" " value={stateCalc.delivery} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label="   " value={stateCalc.earningsNoDelivery} />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={4}>
                <Grid container rowSpacing={1.5} spacing={3}>
                    <Grid item xs={12}>
                        <TextFieldCustom
                            label="     (EBITDA)"
                            value={stateCalc.ebitda}
                            InputProps={{
                                readOnly: true,
                                inputComponent: MoneyFormatInput,
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <Tooltip title="   ">
                                            <HelpOutlineIcon
                                                sx={{
                                                    color: 'action.active',
                                                    marginRight: (theme) => theme.spacing(1),
                                                    marginY: (theme) => theme.spacing(0.5)
                                                }}
                                            />
                                        </Tooltip>
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label=" , " value={stateCalc.tax} />
                    </Grid>
                    <Grid item xs={12}>
                        <TextFieldCustomMoney label="" value={stateCalc.earningsNoTax} />
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    );
}
